<template>
    <div class="lottery">
        <div class="title"><h4>每次抽奖消耗 20 颗v豆</h4></div>
        <section>
            <div v-for="(item, index) in textdata.flops" @click="handle(index)" :class="[show === index?'active':'','lottery-item']"><img :src="item.imageUrl"></div>
        </section>
    </div>
</template>
<script>
    import config from '../../config'
    export default {
        props: {
            textdata:{
                type: Object,
                default: function () {
                    return {}
                }
            }
        },
        data () {
            return {
                show: null,
                loaded: null,
                anmimatonEnd: null
            }
        },
        watch: {

        },
        methods: {
            handle (index) {
                
                if(!this.$store.state.userInfo.login) return alert('点击签到按钮登录先')
                this.show = index
                this.$http.get(config.url+'/lottery')
                .then(response => {
                    let alldata = JSON.parse(response.body)
                    if (alldata.code !== 200) return alert('接口错误')
                    this.$store.dispatch('setAward', alldata.value)
                    this.loaded = true
                    setTimeout(() => {
                        let pop = this.$store.state.award.status?'award':'notAward'
                        this.show = null
                        this.$store.dispatch('setPop', pop)
                    } , 1000)
                })
                .catch(error => {
                    console.log(error);
                })
            }
        }
    }
</script>
<style lang= "scss" scoped>
    .lottery{
        width: 100%;
        .title{
            /*color: rgba(255, 232, 155, 1);*/
            color: #eee;
            text-align: center;
            position: relative;
            padding: .75rem 0 ;
            /*width: 90%;*/
            margin: 0 .9rem;
            height: .7rem;
            line-height: .7rem;
            z-index: 3;
            h4{
                /*position: absolute;*/
                z-index: 2;
                display: inline-block;
                font-size: 0.5rem;
                /*color: #da9494;*/
                background-color: rgba(0, 167, 138, 1);
                z-index: 3;
                margin: 0 auto;
                padding: 0 1rem 0 1rem;
                &:after{
                    content: '';
                    width: 4rem;
                    height: 1px;
                    background-color: #eee;
                    position: absolute;
                    left: 0;
                    top: 1.2rem;
                    z-index: 1;
                }
                &:before{
                    content: '';
                    width: 4rem;
                    height: 1px;
                    background-color: #eee;
                    position: absolute;
                    right: 0;
                    top: 1.2rem;
                    z-index: 1;
                }
            }
        }
        section{
            /*width: 100%;*/
            margin: 0 .65rem;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            .lottery-item{
                border-radius: .1rem;
                border: 2px solid rgba(113, 27, 0, 1);
                width: 5.13rem;
                height: 6.96rem;
                img{
                    width: 100%;
                    height: 100%;
                }
            }
            .active{
                animation: shake 1.2s infinite ease-in-out;
                -webkit-animation: shake 1.2s infinite ease-in-out;
            }
        } 
    }

/**/
@-webkit-keyframes shake {
    0%, 20%, 40%, 60%, 80%, 100% { -webkit-transform: rotate(0deg) } 
    10%, 50%, 90% { -webkit-transform: rotate(-10deg) }
    30%, 70% { -webkit-transform: rotate(10deg) }
}

@keyframes shake {
    0%, 20%, 40%, 60%, 80%, 100% {
     transform: rotate(0deg);
     -webkit-transform: rotate(0deg);
    } 
    10%, 50%, 90% { 
        transform: rotate(-10deg);
        -webkit-transform: rotate(-10deg); 
    }
    30%, 70% { 
        transform: rotate(10deg);
        -webkit-transform: rotate(10deg); 
    }
}
</style>


